import React, { Component } from 'react';
import './styles.css';

class AboutBaitTypes extends Component {
  render() {
    const baitTypes = [
      {
        image: 'https://static.pexels.com/photos/192454/pexels-photo-192454.jpeg',
        type: '',
        text: '    ,     ,     -    .      ,        .    -  !          .    .     ,    ,        .'
      },
      {
        image: 'https://static.pexels.com/photos/294674/pexels-photo-294674.jpeg',
        type: '',
        text: '     ,   ,        .               ,     .   ,      ,        ,         .       ,     1-3.            .'
      },
      {
        image: 'https://static.pexels.com/photos/235419/pexels-photo-235419.jpeg',
        type: '',
        text: '        ,         ,    ,         ,    .     ,          , ,        .   ,    : ,  , ,    ..       .'
      }
    ];

    return (
      <section className="about-bait-types-section">
        <div className="content">
          <h2 className="about-bait-types-section__title heading-2 title-style">
              - 
          </h2>
        </div>

        <div className="about-bait-types">
          <div className="content">
          
          {
            baitTypes.map( (el, index) => 
              <div className={`bait-types-item bait-types-item-${index}`} key={index}>
                <div className="bait-types-item__content">
                  <h3 className="bait-types-item__title heading-4">
                    {el.type}
                  </h3>
                  <div className="bait-types-item__image-wrapper">
                    <img src={el.image} alt={el.type} title={el.type} />
                  </div>
                  <p className="bait-types-item__text text-style">
                    {el.text}
                  </p>
                </div>
              </div>
            )
          }

          </div>
        </div>
      </section>
    );
  }
}

export default AboutBaitTypes;


